@import url('https://fonts.googleapis.com/css2?family=Kumbh+Sans:wght@400;700&family=Manrope:wght@200;300;400;500;600;700;800&display=swap');

$main-font-color:#333333;
$main-font-family: 'Manrope', sans-serif;
$main-bg-color:#F7F6FF;
$main-font-color:  #333333;
$main-theme-color: linear-gradient(115.11deg, #F32C6D 10.12%, #CB1D95 100.01%);


@mixin button {
    background: linear-gradient(115.11deg, #F32C6D 10.12%, #CB1D95 100.01%);
    font-size: 1rem;
    border: none;
    outline: none;
    font-weight: 600;
    font-size: 1rem;
    color: #fff;
    padding: 14px 42px;
    border-radius: 9px;
    align-self: center;
    text-align: center;
    cursor: pointer;
    
}
    
* {
    margin: 0;
    padding: 0;
    text-decoration: none;
    box-sizing: border-box;
    font-family: $main-font-family;
    color: $main-font-color;
    // background-color: $main-bg-color;
   
}

body {
    height: 100vh;
}

.container {
